import { useRef, useState, useMemo } from 'react';
import { motion, useInView } from 'framer-motion';
import MusicianCard from '../musicians/MusicianCard';
import { musicians } from '../../data/musicians';

type SortOption = 'id' | 'name' | 'genre';

const MusiciansSection = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });
  const [selectedGenre, setSelectedGenre] = useState<string>('all');
  const [sortOption, setSortOption] = useState<SortOption>('id');

  // Extract all unique genres
  const allGenres = useMemo(() => {
    const genreSet = new Set<string>();
    musicians.forEach(musician => {
      musician.genre.forEach(g => genreSet.add(g));
    });
    return Array.from(genreSet).sort();
  }, []);

  // Filter and sort musicians
  const filteredMusicians = useMemo(() => {
    let filtered = [...musicians];

    // Filter by genre
    if (selectedGenre !== 'all') {
      filtered = filtered.filter(musician =>
        musician.genre.includes(selectedGenre)
      );
    }

    // Sort
    filtered.sort((a, b) => {
      switch (sortOption) {
        case 'name':
          return a.name.localeCompare(b.name, 'ko');
        case 'genre':
          return a.genre[0].localeCompare(b.genre[0], 'ko');
        case 'id':
        default:
          return a.id - b.id;
      }
    });

    return filtered;
  }, [selectedGenre, sortOption]);

  return (
    <section id="musicians" className="section bg-ocean-sand" ref={ref}>
      <div className="container mx-auto px-4 py-16">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h2 className="text-4xl font-medium text-gray-900 mb-4 font-serif">
            참여 뮤지션
          </h2>
          <p className="text-lg text-gray-600 subtitle">
            12팀의 뮤지션들이 전하는 평화의 메시지
          </p>
        </motion.div>

        {/* Filter and Sort Controls */}
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 10 }}
          transition={{ duration: 0.6, delay: 0.2 }}
          className="flex flex-col sm:flex-row gap-4 justify-center items-center mb-12 flex-wrap"
        >
          {/* Genre Filter */}
          <div className="flex items-center gap-3">
            <label className="text-sm font-bold text-gray-700">장르:</label>
            <select
              value={selectedGenre}
              onChange={(e) => setSelectedGenre(e.target.value)}
              className="px-4 py-2 border-2 border-jeju-ocean text-jeju-ocean rounded-full font-medium hover:bg-jeju-ocean hover:text-white transition-colors cursor-pointer bg-white"
            >
              <option value="all">전체</option>
              {allGenres.map(genre => (
                <option key={genre} value={genre}>{genre}</option>
              ))}
            </select>
          </div>

          {/* Sort Option */}
          <div className="flex items-center gap-3">
            <label className="text-sm font-bold text-gray-700">정렬:</label>
            <select
              value={sortOption}
              onChange={(e) => setSortOption(e.target.value as SortOption)}
              className="px-4 py-2 border-2 border-jeju-ocean text-jeju-ocean rounded-full font-medium hover:bg-jeju-ocean hover:text-white transition-colors cursor-pointer bg-white"
            >
              <option value="id">추천순</option>
              <option value="name">이름순</option>
              <option value="genre">장르순</option>
            </select>
          </div>

          {/* Result Count */}
          <span className="text-sm text-coastal-gray font-medium ml-2">
            {filteredMusicians.length}팀
          </span>
        </motion.div>

        {/* Musicians Grid */}
        {filteredMusicians.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {filteredMusicians.map((musician, index) => (
              <MusicianCard
                key={musician.id}
                musician={musician}
                index={index}
              />
            ))}
          </div>
        ) : (
          <div className="text-center py-16 text-gray-500">
            <p className="text-lg">해당하는 뮤지션이 없습니다.</p>
          </div>
        )}
      </div>
    </section>
  );
};

export default MusiciansSection;
